const colorGreen = "#95de64"
const colorRed = "#ffa39e";
const colorYellow = "#ffd591"

export const sortPageContent = (page) => {
  page.content = page.content.filter((item) => {
    return item.str.length > 0;
  });
  page.content.sort((a, b) =>
    a.y > b.y ? 1 : a.y === b.y ? (a.x > b.x ? 1 : -1) : -1
  );
  return page;
};

export const groupPageContent = (page) => {
  page.content = page.content.map((item) => {
    return [item];
  });
  return page;
};

export const groupPageContentByY = (page) => {
  page.content = page.content.reduce((group, item) => {
    const { y } = item;
    // console.log("group", group, "current", item, "y", y);
    if (group.length === 0) {
      group.push([item]);
      return group;
    } else if (
      group[group.length - 1][0].y > y + 5 ||
      group[group.length - 1][0].y < y - 5
    ) {
      group.push([item]);
      return group;
    } else {
      group[group.length - 1].push(item);
      return group;
    }
  }, []);
  return page;
};

export const sortPageContentByX = (page) => {
  page.content = page.content.map((line) => {
    line.sort((a, b) => (a.x > b.x ? 1 : -1));
    return line;
  });
  return page;
};

export const groupPageContentByX = (page) => {
  page.content = page.content.map((line) => {
    line = line.reduce((_line, item, idx) => {
      if (_line.length === 0) {
        _line.push(item);
        return _line;
      } else if (
        item.str !== " " &&
        item.str !== "" &&
        line[idx - 1] !== " " &&
        item.x < _line[_line.length - 1].x + _line[_line.length - 1].width + 10
      ) {
        if (
          item.x >
          _line[_line.length - 1].x + _line[_line.length - 1].width + 5
        ) {
          _line[_line.length - 1].str += "  " + item.str;
        } else {
          _line[_line.length - 1].str += item.str;
        }
        _line[_line.length - 1].width =
          item.x + item.width - _line[_line.length - 1].x;
        return _line;
      } else if (item.str === " " || item.str === "") {
        return _line;
      } else {
        _line.push(item);
        return _line;
      }
    }, []);
    return line;
  });
  return page;
};

export const formatPageToTable = (page) => {
  console.log(page);
  let colNum = 1;
  let baseline = [];
  page.content.forEach((line) => {
    if (line.length > colNum) {
      colNum = line.length;
      baseline = line;
    }
  });
  if (colNum > 1) {
    page.content = page.content.map((line) => {
      if (line.length !== colNum) {
        line = line.reduce((_line, item) => {
          for (let i = 0; i < colNum; i++) {
            let left = i === 0 ? 0 : baseline[i - 1].x + baseline[i - 1].width;
            let right =
              i === colNum - 1
                ? page.pageInfo.width
                : baseline[i].x + baseline[i].width;
            if (item.x >= left && item.x < right) {
              if (_line[i].str === "") {
                _line[i] = item;
                break;
              } else {
                _line[i].str += item.str;
                break;
              }
            }
          }
          return _line;
        }, new Array(colNum).fill({ str: "", color: "#efefef" }));
      }
      line = line.map((item) => {
        item.table = true;
        return item;
      });
      return line;
    });
  }
  return page;
};

export const compareNumbersInPage = (pL, pR) => {
  // find
  let numL = [];
  let numGroupedByLineL = [];
  pL.content.forEach((line) => {
    let numLine = [];
    line.forEach((cell) => {
      let numbers = cell.str.match(/[0-9]+/g);
      if (numbers && numbers.length > 0) {
        cell.number = numbers.join("-");
        numL.push(cell.number);
        numLine.push(cell.number);
        cell.color = colorRed;
      }
    });
    numGroupedByLineL.push(numLine.join("#"));
  });
  let numR = [];
  let numGroupedByLineR = [];
  pR.content.forEach((line) => {
    let numLine = [];
    line.forEach((cell) => {
      let numbers = cell.str.match(/[0-9]+/g);
      if (numbers && numbers.length > 0) {
        cell.number = numbers.join("-");
        numR.push(cell.number);
        numLine.push(cell.number);
        cell.color = colorRed;
      }
    });
    numGroupedByLineR.push(numLine.join("#"));
  });
  // compare cell by cell;
  pL.content.forEach((line) => {
    line.forEach((cell) => {
      if (cell.number && numR.includes(cell.number)) {
        cell.color = colorGreen;
      }
    });
  });
  pR.content.forEach((line) => {
    line.forEach((cell) => {
      if (cell.number && numL.includes(cell.number)) {
        cell.color = colorGreen;
      }
    });
  });
  // compare line by line;
  pL.content.forEach((line) => {
    let num = [];
    line.forEach((cell) => {
      if (cell.number) {
        num.push(cell.number);
      }
    });
    if (!numGroupedByLineR.includes(num.join("#"))) {
      line.forEach((cell) => {
        if (cell.number && cell.color === colorGreen) {
          cell.color = colorYellow;
        }
      });
    }
  });
  pR.content.forEach((line) => {
    let num = [];
    line.forEach((cell) => {
      if (cell.number) {
        num.push(cell.number);
      }
    });
    if (!numGroupedByLineL.includes(num.join("#"))) {
      line.forEach((cell) => {
        if (cell.number && cell.color === colorGreen) {
          cell.color = colorYellow;
        }
      });
    }
  });
};
