import * as pdfjsLib from "pdfjs-dist/build/pdf";
import worker from "pdfjs-dist/webpack";
pdfjsLib.GlobalWorkerOptions.workerSrc = worker;

class PdfExtract {
  extract(data, cb) {
    if (!cb) {
      return new Promise((resolve, reject) => {
        this.extract(data, (err, data) => {
          if (err) {
            reject(err);
          } else {
            resolve(data);
          }
        });
      });
    }
    const pdf = {
      meta: {},
      pages: [],
    };
    console.assert(data instanceof ArrayBuffer || typeof data == "string");
    let options = {};
    options.url = data;
    options.cMapUrl = `//cdn.jsdelivr.net/npm/pdfjs-dist@${pdfjsLib.version}/cmaps/`;
    pdfjsLib
      .getDocument(options)
      .promise.then((doc) => {
        const total = doc.numPages;
        const promises = [
          doc.getMetadata().then((data) => {
            pdf.meta = {
              info: data.info,
              metadata: data.metadata ? data.metadata.getAll() || null : null,
            };
          }),
        ];
        const loadPage = (pageNum) =>
          doc.getPage(pageNum).then((page) => {
            const viewport = page.getViewport({ scale: 1.0 });
            const _page = {
              pageInfo: {
                num: pageNum,
                scale: viewport.scale,
                rotation: viewport.rotation,
                offsetX: viewport.offsetX,
                offsetY: viewport.offsetY,
                width: viewport.width,
                height: viewport.height,
              },
            };
            pdf.pages.push(_page);
            const normalizeWhitespace = !!(
              options && options.normalizeWhitespace === true
            );
            const disableCombineTextItems = !!(
              options && options.disableCombineTextItems === true
            );
            return Promise.all([
              page.getAnnotations().then((annotations) => {
                _page.links = annotations
                  .filter((annot) => annot.subtype === "Link" && !!annot.url)
                  .map((link) => link.url);
              }),
              page
                .getTextContent({
                  normalizeWhitespace,
                  disableCombineTextItems,
                })
                .then((content) => {
                  _page.content = content.items.map((item) => {
                    const tm = item.transform;
                    let x = tm[4];
                    let y = _page.pageInfo.height - tm[5];
                    if (viewport.rotation === 90) {
                      x = tm[5];
                      y = tm[4];
                    }
                    // see https://github.com/mozilla/pdf.js/issues/8276
                    const height = Math.sqrt(tm[2] * tm[2] + tm[3] * tm[3]);
                    return {
                      x: x,
                      y: y,
                      str: item.str,
                      dir: item.dir,
                      width: item.width,
                      height: height,
                      fontName: item.fontName,
                    };
                  });
                }),
            ]).then(
              () => {
                console.log("done");
              },
              (err) => {
                cb(err);
              }
            );
          });
        for (let i = 1; i <= total; i++) {
          promises.push(loadPage(i));
        }
        return Promise.all(promises);
      })
      .then(
        () => {
          pdf.pages.sort((a, b) => a.pageInfo.num - b.pageInfo.num);
          cb(null, pdf);
        },
        (err) => {
          cb(err);
        }
      );
  }
}

export default PdfExtract;
