import './App.css'

import { useState } from 'react'
import { Button, Pagination, Modal } from 'antd'
import { BlockOutlined } from '@ant-design/icons'

import PdfButton from './PdfButton'
import PdfView from './PdfView'

import {
  sortPageContent,
  sortPageContentByX,
  groupPageContentByY,
  groupPageContentByX,
  formatPageToTable,
  compareNumbersInPage,
} from './PdfUtil'

const info = () => {
  Modal.info({
    title: '标注色彩说明',
    content: (
      <div>
        <p><b>绿色</b>&nbsp;在另一侧找到含有相同数字的区域，且区域所在行中的数字完全相同</p>
        <p><b>黄色</b>&nbsp;在另一侧找到含有相同数字的区域，但区域所在行中的数字不完全相同</p>
        <p><b>红色</b>&nbsp;在另一侧没有找到含有相同的数字的区域</p>
      </div>
    ),
    onOk() {},
  });
};

const deepCopyData = (data) => {
  return JSON.parse(JSON.stringify(data))
}

function App() {
  const [pdfLeftRaw, setPdfLeftRaw] = useState({})
  const [pdfRightRaw, setPdfRightRaw] = useState({})
  const [pdfLeft, setPdfLeft] = useState({})
  const [pdfRight, setPdfRight] = useState({})
  const [pdfLeftPage, setPdfLeftPage] = useState(1)
  const [pdfRightPage, setPdfRightPage] = useState(1)

  const loadPdfLeft = (raw) => {
    setPdfLeftRaw(raw)
    setPdfLeft(parseFile(raw))
  }
  const loadPdfRight = (raw) => {
    setPdfRightRaw(raw)
    setPdfRight(parseFile(raw))
  }

  const parseFile = (raw) => {
    let target = deepCopyData(raw)
    target.pages = target.pages.map((page) => {
      page = sortPageContent(page)
      page = groupPageContentByY(page)
      page = sortPageContentByX(page)
      page = groupPageContentByX(page)
      return page
    })
    return target
  }

  const formatPlain = async (side = 'left') => {
    let raw
    if (side === 'left') {
      raw = deepCopyData(pdfLeftRaw)
      if ('meta' in raw) {
        setPdfLeft(parseFile(raw))
      }
    }
    if (side === 'right') {
      raw = deepCopyData(pdfRightRaw)
      if ('meta' in raw) {
        setPdfRight(parseFile(raw))
      }
    }
  }

  const formatTable = (side = 'left') => {
    let raw
    if (side === 'left') raw = deepCopyData(pdfLeftRaw)
    if (side === 'right') raw = deepCopyData(pdfRightRaw)
    console.log(raw)
    if ('meta' in raw) {
      raw.pages = raw.pages.map((page) => {
        page = sortPageContent(page)
        page = groupPageContentByY(page)
        page = sortPageContentByX(page)
        page = groupPageContentByX(page)
        page = formatPageToTable(page)
        return page
      })
      if (side === 'left') setPdfLeft(raw)
      if (side === 'right') setPdfRight(raw)
    }
  }

  const compareNumbersByTwoPage = () => {
    if ('meta' in pdfLeft && 'meta' in pdfRight) {
      let _fL = deepCopyData(pdfLeft)
      let _fR = deepCopyData(pdfRight)
      compareNumbersInPage(
        _fL.pages[pdfLeftPage - 1],
        _fR.pages[pdfRightPage - 1],
      )
      setPdfLeft(_fL)
      setPdfRight(_fR)
    }
  }

  return (
    <div className="App">
      <div className="header">
        <BlockOutlined />
        <div style={{ flex: 1 }}></div>
        <Button type="primary" onClick={() => compareNumbersByTwoPage()}>
          对比两侧数字
        </Button>
        <Button
          onClick={info}
        >
          说明
        </Button>
      </div>
      <div className="container">
        <div className="content">
          <div className="content-head">
            <PdfButton loadPdf={loadPdfLeft} />
            <div style={{ flex: 1 }}></div>
            <Button onClick={() => formatPlain('left')}>普通视图</Button>
            <Button onClick={() => formatTable('left')}>表格视图 BETA</Button>
          </div>
          <div className="content-main">
            <PdfView
              pdfFile={pdfLeft}
              pdfPage={pdfLeftPage}
              style={{ overflow: 'auto' }}
            />
          </div>
          {'pages' in pdfLeft && (
            <div className="content-foot">
              <Pagination
                onChange={(e) => {
                  setPdfLeftPage(e)
                }}
                pageSize={1}
                total={pdfLeft.pages.length}
              />
            </div>
          )}
        </div>
        <div className="content">
          <div className="content-head">
            <PdfButton loadPdf={loadPdfRight}/>
            <div style={{ flex: 1 }}></div>
            <Button onClick={() => formatPlain('right')}>普通视图</Button>
            <Button onClick={() => formatTable('right')}>表格视图 BETA</Button>
          </div>
          <div className="content-main">
            <PdfView
              pdfFile={pdfRight}
              pdfPage={pdfRightPage}
              style={{ overflow: 'auto' }}
            />
          </div>
          {'pages' in pdfRight && (
            <div className="content-foot">
              <Pagination
                onChange={(e) => {
                  setPdfRightPage(e)
                }}
                pageSize={1}
                total={pdfRight.pages.length}
              />
            </div>
          )}
        </div>
      </div>
    </div>
  )
}

export default App
