import { Typography, Collapse, List } from "antd";
const { Text, Link } = Typography;
const { Panel } = Collapse;

function PdfView(props) {
  return (
    <div style={{ padding: 8 }}>
      {props.pdfFile.pages && (
        <List
          dataSource={props.pdfFile.pages[props.pdfPage - 1].content}
          renderItem={(line) => (
            <List.Item style={{ padding: 2 }}>
              <div
                style={{
                  display: "flex",
                  flex: "1",
                  gap: 12,
                }}
              >
                {line.map((cell, index) => (
                  <div
                    key={index}
                    style={{
                      backgroundColor: cell.color,
                      flex: cell.table ? "1" : "",
                    }}
                  >
                    {cell.str}
                  </div>
                ))}
              </div>
            </List.Item>
          )}
        />
      )}
    </div>
  );
}

export default PdfView;
